'use client'

import { useEffect } from 'react'
import { useRouter } from 'next/navigation'
import supabase from '@/lib/supabaseBrowserClient'

export default function AuthHandler() {
  const router = useRouter()

  useEffect(() => {
    const handleAuth = async () => { // handle the authentication process asynchronously
      const hash = window.location.hash
      const params = new URLSearchParams(hash.replace('#', '?'))

      const accessToken = params.get('access_token') // the access token from the URL hash
      const refreshToken = params.get('refresh_token') // the refresh token from the URL hash
      const type = params.get('type') // the type of authentication, e.g., 'magiclink'

      console.log('Access token:', accessToken)
      console.log('Refresh token:', refreshToken)
      console.log('Type:', type)

      if (!accessToken || !refreshToken || type !== 'magiclink') { // check if the tokens and type are present/correct
        console.warn('missing info â€” skipping session setup.')
        return
      }

      const { error } = await supabase.auth.setSession({ // set the session with the access and refresh tokens
        access_token: accessToken,
        refresh_token: refreshToken,
      })

      if (error) {
        console.error('setSession error:', error)
      } else {
        console.log('Session established successfully')
        router.replace('/')
      }
    }

    handleAuth()
  }, [router])

  return null
}
